import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'periodicidade-dados',   templateUrl: 'periodicidade_dados.component.html' }) export class PeriodicidadeDadosComponent implements OnInit {       displayMode: PeriodicidadeDadosDisplayModeEnum;     displayModeEnum = PeriodicidadeDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = PeriodicidadeDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = PeriodicidadeDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = PeriodicidadeDadosDisplayModeEnum.Edit;           break;       }     }  }  enum PeriodicidadeDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


