import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Periodicidade } from '../../../modelo/Periodicidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'periodicidade-dados-details',   templateUrl: 'periodicidade_dadosDetails.component.html' }) export class PeriodicidadeDadosDetailsComponent implements OnInit {    periodicidade: Periodicidade;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.periodicidade.Periodicidade";                 let id = +params['id'];         this.periodicidade = new Periodicidade();         this.periodicidade.cd_periodicidade = id;         o.objetoJson = this.periodicidade;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a periodicidade. '; }      this.periodicidade = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


